.container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
}

.container button {
    background-color: #242424;
    border: none;
    padding: 5px 10px;
    border-radius: 3px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

button svg {
    width: 10px;  
}

button[disabled] {
    background-color: #474747;
    cursor: default;
}

.container input {
    height: 30px;
    width: 40px;
    text-align: center;
    border: 1px solid #242424;
    border-radius: 3px; 
}
